
<template>
<div>
  <add></add>
  <el-table
    :data="tableData4"
    style="width: 100%"
    max-height="250">
    <el-table-column
      prop="id"
      label="图书编号"
      align="center"
      >
    </el-table-column>
    <el-table-column
      prop="name"
      label="图书名称"
      align="center"

   >
    </el-table-column>
    <el-table-column
      prop="author"
      label="作者"
      align="center"
     >
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      align="center"
      >
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="deleteRow(scope.$index, tableData4)"
          type="text"
          size="small">
          移除
        </el-button>
      </template>
    </el-table-column>
  </el-table>
    <el-button @click="logout" style="float:right;margin:5%" >退出登录</el-button>
  </div>
</template>

<script>
import add from '../components/addButton'
import { Message } from 'element-ui';
export default {
  methods: {
    logout(){
      this.$confirm("确定退出登录吗？","提示",{
        confirmButtonText:"确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(()=>{
        this.$http({
          url: "http://120.24.5.178:8100/user/logout",
          method:"GET",
          headers: {
                "Content-Type": "application/json"
              },
          withCredentials: true
        }).then(
             function(response){
               console.log(response.data.code)
             if(response.data.code === 0){
                Message.success("退出成功！")
                this.$router.push({name:"login"})
             }else{
               Message.warning(response.data.msg)
             }
        })
      })
    },
    deleteRow(index, rows) {
      this.$http({
        url: ""
      })
      rows.splice(index, 1);
    }
  },
  data() {
    return {
      tableData4: [
        {
          id: "1",
          name: "java",
          author: "111"
        }
      ]
    };
  }
};
</script>




